// Colors

$bg-banner-desk: conic-gradient(
  from 130.31deg at 16.03% 41.06%,
  #b80027 -46.69deg,
  #003d85 32.24deg,
  #b80027 313.31deg,
  #003d85 392.24deg
);
$bg-banner-tab: conic-gradient(
  from 139.4deg at 20.7% 35.74%,
  #b80027 -46.69deg,
  #003d85 32.24deg,
  #b80027 313.31deg,
  #003d85 392.24deg
);
$bg-banner-mob: conic-gradient(
  from 171.43deg at 35.31% 34.22%,
  #b80027 -46.69deg,
  #003d85 32.24deg,
  #b80027 313.31deg,
  #003d85 392.24deg
);
$border-gradient: linear-gradient(143deg, #d10a42, #00359f);
$button-gradient: linear-gradient(97.05deg, #ed0233 17.6%, #003eb7 95.31%);
$number-gradient: linear-gradient(127.61deg, #d10a42 0%, #0041ca 45.58%);
$number-gradient2: linear-gradient(142.51deg, #d10a42 0%, #0041ca 87.33%);
$number-gradient-mob: linear-gradient(-377.51deg, #0041ca 39%, #d10a42 71.33%);
$number-gradient-mob2: linear-gradient(-377.51deg, #0041ca 53%, #d10a42 71.33%);
$bg-gradient-footer-top: linear-gradient(
  to right,
  #102654 50%,
  transparent 50%,
  transparent
);
$resolution-blue: #1c3374;
$color-sapphire: #102654;
$color-ghost-white: #f8f9fc;
$color-gray: #787878;
$color-crimson: #d10a42;
$color-chambray: #495a7d;
$color-egyptian-blue: #103aae;
$color-whisper: #e8e8e8;
$color-torch-red: #ed0233;
$color-spindle: #b6bdcc;
$color-persian-blue: #2e35ac;
$bg-cornflower-blue: linear-gradient(#426ddd, #aecff2);
$light-slate-grey: #74819c;
$bg-black-russian: #030a1a;

// Default

$color-default-black: #000000;
$color-default-white: #ffffff;
$fz-default: 16px;

// Fonts

$ff-bebas-neue: "Bebas Neue", "Tahoma", sans-serif;
$ff-roboto-condensed: "Roboto Condensed", "Tahoma", sans-serif;

// Animation
// ---------------------------------

$tf-default: ease;
$trans-default: 0.3s $tf-default;

//      $tf-default,     
$trans-modal: 0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600: 0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320: 320px;
$vp-321: 321px;
$vp-350: 350px;
$vp-351: 351px;
$vp-768: 768px;
$vp-1200: 1200px;
$vp-1280: 1280px;

// Retina
// ---------------------------------

$retina-dpi: 144dpi;
$retina-dppx: 1.5dppx;
